import createElement from "./createElement";
import patch from "./patch";
export default function updateChildren(parentElm, oldCh, newCh) {
  // Âõõ‰∏™ÊåáÈíà
  // ÊóßÂâç
  let oldStartIdx = 0;
  // Êñ∞Ââç
  let newStartIdx = 0;
  // ÊóßÂêé
  let oldEndIdx = oldCh.length - 1;
  // Êñ∞Âêé
  let newEndIdx = newCh.length - 1;

  // ÊåáÈíàÊåáÂêëÁöÑÂõõ‰∏™ËäÇÁÇπ
  // ÊóßÂâçËäÇÁÇπ
  let oldStartVnode = oldCh[0];
  // ÊóßÂêéËäÇÁÇπ
  let oldEndVnode = oldCh[oldEndIdx];
  // Êñ∞ÂâçËäÇÁÇπ
  let newStartVnode = newCh[0];
  // Êñ∞ÂêéËäÇÁÇπ
  let newEndVnode = newCh[newEndIdx];
  //mapÊò†Â∞Ñ
  let keyMap = null;

  while (oldStartIdx <= oldEndIdx && newStartIdx <= newEndIdx) {
    // È¶ñÂÖàÂ∫îËØ•‰∏çÊòØÂà§Êñ≠ÂõõÁßçÂëΩ‰∏≠ÔºåËÄåÊòØÁï•ËøáÂ∑≤ÁªèÂä†‰∫ÜundefinedÊ†áËÆ∞ÁöÑÈ°π
    if (oldCh[oldStartIdx] === undefined) {
      oldStartVnode = oldCh[++oldStartIdx];
    } else if (oldCh[oldEndIdx] === undefined) {
      oldEndVnode = oldCh[--oldEndIdx];
    } else if (newCh[newStartIdx] === undefined) {
      newStartVnode = newCh[++newStartIdx];
    } else if (newCh[newEndIdx] === undefined) {
      newEndVnode = newCh[--newEndIdx];
    } else if (checkSameVnode(oldStartVnode, newStartVnode)) {
      // Êñ∞Ââç‰∏éÊóßÂâç
      console.log("Êñ∞Ââç‰∏éÊóßÂâçÂëΩ‰∏≠");
      // Á≤æÁªÜÂåñÊØîËæÉ‰∏§‰∏™ËäÇÁÇπ oldStartVnodeÁé∞Âú®ÂíånewStartVnode‰∏ÄÊ†∑‰∫Ü
      patch(oldStartVnode, newStartVnode);
      // ÁßªÂä®ÊåáÈíàÔºåÊîπÂèòÊåáÈíàÊåáÂêëÁöÑËäÇÁÇπÔºåËøôË°®Á§∫Ëøô‰∏§‰∏™ËäÇÁÇπÈÉΩÂ§ÑÁêÜÔºàÊØîËæÉÔºâÂÆå‰∫Ü
      oldStartVnode = oldCh[++oldStartIdx];
      newStartVnode = newCh[++newStartIdx];
    } else if (checkSameVnode(oldEndVnode, newEndVnode)) {
      // Êñ∞Âêé‰∏éÊóßÂêé
      console.log("Êñ∞Âêé‰∏éÊóßÂêéÂëΩ‰∏≠");
      patch(oldEndVnode, newEndVnode);
      oldEndVnode = oldCh[--oldEndIdx];
      newEndVnode = newCh[--newEndIdx];
    } else if (checkSameVnode(oldStartVnode, newEndVnode)) {
      // Êñ∞Âêé‰∏éÊóßÂâç
      console.log("Êñ∞Âêé‰∏éÊóßÂâçÂëΩ‰∏≠");
      patch(oldStartVnode, newEndVnode);
      // ÂΩì‚ë¢Êñ∞Âêé‰∏éÊóßÂâçÂëΩ‰∏≠ÁöÑÊó∂ÂÄôÔºåÊ≠§Êó∂Ë¶ÅÁßªÂä®ËäÇÁÇπ„ÄÇÁßªÂä® Êñ∞ÂêéÔºàÊóßÂâçÔºâ ÊåáÂêëÁöÑËøô‰∏™ËäÇÁÇπÂà∞ËÄÅËäÇÁÇπÁöÑ ÊóßÂêéÁöÑÂêéÈù¢
      // ÁßªÂä®ËäÇÁÇπÔºöÂè™Ë¶ÅÊèíÂÖ•‰∏Ä‰∏™Â∑≤ÁªèÂú®DOMÊ†ë‰∏ä ÁöÑËäÇÁÇπÔºåÂ∞±‰ºöË¢´ÁßªÂä®
      parentElm.insertBefore(oldStartVnode.elm, oldEndVnode.elm.nextSibling);
      oldStartVnode = oldCh[++oldStartIdx];
      newEndVnode = newCh[--newEndIdx];
    } else if (checkSameVnode(oldEndVnode, newStartVnode)) {
      // Êñ∞Ââç‰∏éÊóßÂêé
      console.log("Êñ∞Ââç‰∏éÊóßÂêéÂëΩ‰∏≠");
      patch(oldEndVnode, newStartVnode);
      // ÂΩì‚ë£Êñ∞Ââç‰∏éÊóßÂêéÂëΩ‰∏≠ÁöÑÊó∂ÂÄôÔºåÊ≠§Êó∂Ë¶ÅÁßªÂä®ËäÇÁÇπ„ÄÇÁßªÂä® Êñ∞ÂâçÔºàÊóßÂêéÔºâ ÊåáÂêëÁöÑËøô‰∏™ËäÇÁÇπÂà∞ËÄÅËäÇÁÇπÁöÑ ÊóßÂâçÁöÑÂâçÈù¢
      // ÁßªÂä®ËäÇÁÇπÔºöÂè™Ë¶ÅÊèíÂÖ•‰∏Ä‰∏™Â∑≤ÁªèÂú®DOMÊ†ë‰∏äÁöÑËäÇÁÇπÔºåÂ∞±‰ºöË¢´ÁßªÂä®
      parentElm.insertBefore(oldEndVnode.elm, oldStartVnode.elm);
      oldEndVnode = oldCh[--oldEndIdx];
      newStartVnode = newCh[++newStartIdx];
    } else {
      // ÂõõÁßçÈÉΩÊ≤°ÊúâÂåπÈÖçÂà∞ÔºåÈÉΩÊ≤°ÊúâÂëΩ‰∏≠
      console.log("ÂõõÁßçÈÉΩÊ≤°ÊúâÂëΩ‰∏≠");
      // ÂØªÊâæ keyMap ‰∏Ä‰∏™Êò†Â∞ÑÂØπË±°Ôºå Â∞±‰∏çÁî®ÊØèÊ¨°ÈÉΩÈÅçÂéÜoldÂØπË±°‰∫Ü
      if (!keyMap) {
        keyMap = {};
        // ËÆ∞ÂΩïoldVnode‰∏≠ÁöÑËäÇÁÇπÂá∫Áé∞ÁöÑkey
        // ‰ªéoldStartIdxÂºÄÂßãÂà∞oldEndIdxÁªìÊùüÔºåÂàõÂª∫keyMap
        for (let i = oldStartIdx; i <= oldEndIdx; i++) {
          const key = oldCh[i].key;
          if (key !== undefined) {
            keyMap[key] = i;
          }
        }
      }
      // ÂØªÊâæÂΩìÂâçÈ°πÔºànewStartIdxÔºâÂú®keyMap‰∏≠Êò†Â∞ÑÁöÑÂ∫èÂè∑
      const idxInOld = keyMap[newStartVnode.key];
      if (idxInOld === undefined) {
        // Â¶ÇÊûú idxInOld ÊòØ undefined ËØ¥ÊòéÊòØÂÖ®Êñ∞ÁöÑÈ°πÔºåË¶ÅÊèíÂÖ•
        // Ë¢´Âä†ÂÖ•ÁöÑÈ°πÔºàÂ∞±ÊòØnewStartVnodeËøôÈ°π)Áé∞‰∏çÊòØÁúüÊ≠£ÁöÑDOMËäÇÁÇπ
        parentElm.insertBefore(createElement(newStartVnode), oldStartVnode.elm);
      } else {
        // ËØ¥Êòé‰∏çÊòØÂÖ®Êñ∞ÁöÑÈ°πÔºåË¶ÅÁßªÂä®
        const elmToMove = oldCh[idxInOld];
        patch(elmToMove, newStartVnode);
        // ÊääËøôÈ°πËÆæÁΩÆ‰∏∫undefinedÔºåË°®Á§∫ÊàëÂ∑≤ÁªèÂ§ÑÁêÜÂÆåËøôÈ°π‰∫Ü
        oldCh[idxInOld] = undefined;
        // ÁßªÂä®ÔºåË∞ÉÁî®insertBefore‰πüÂèØ‰ª•ÂÆûÁé∞ÁßªÂä®„ÄÇ
        parentElm.insertBefore(elmToMove.elm, oldStartVnode.elm);
      }
      // newStartIdx++;
      newStartVnode = newCh[++newStartIdx];
    }
  }
  // Âæ™ÁéØÁªìÊùü
  if (newStartIdx <= newEndIdx) {
    // ËØ¥ÊòénewVndoeËøòÊúâÂâ©‰ΩôËäÇÁÇπÊ≤°ÊúâÂ§ÑÁêÜÔºåÊâÄ‰ª•Ë¶ÅÊ∑ªÂä†Ëøô‰∫õËäÇÁÇπ
    // // ÊèíÂÖ•ÁöÑÊ†áÊùÜ
    // const before =
    //   newCh[newEndIdx + 1] === null ? null : newCh[newEndIdx + 1].elm;
    for (let i = newStartIdx; i <= newEndIdx; i++) {
      // insertBeforeÊñπÊ≥ïÂèØ‰ª•Ëá™Âä®ËØÜÂà´nullÔºåÂ¶ÇÊûúÊòØnullÂ∞±‰ºöËá™Âä®ÊéíÂà∞ÈòüÂ∞æÔºåÂíåappendChild‰∏ÄËá¥
      parentElm.insertBefore(createElement(newCh[i]), null);
    }
  } else if (oldStartIdx <= oldEndIdx) {
    // ËØ¥ÊòéoldVnodeËøòÊúâÂâ©‰ΩôËäÇÁÇπÊ≤°ÊúâÂ§ÑÁêÜÔºåÊâÄ‰ª•Ë¶ÅÂà†Èô§Ëøô‰∫õËäÇÁÇπ
    for (let i = oldStartIdx; i <= oldEndIdx; i++) {
      if (oldCh[i]) {
        parentElm.removeChild(oldCh[i].elm);
      }
    }
  }
}

// Âà§Êñ≠ÊòØÂê¶ÊòØÂêå‰∏Ä‰∏™ËäÇÁÇπ
function checkSameVnode(a, b) {
  return a.sel === b.sel && a.key === b.key;
}